'use strict';

(function () {
  'use strict';

  angular.module('home').config(config);

  /* jshint -W034 */
  function config($stateProvider) {
    'ngInject';

    $stateProvider.state('home', {
      url: '/',
      templateUrl: 'home/home.tpl.html',
      controller: 'HomeCtrl',
      controllerAs: 'ctrl'
    });
  }
})();
'use strict';

function _classCallCheck(instance, Constructor) { if (!(instance instanceof Constructor)) { throw new TypeError("Cannot call a class as a function"); } }

(function () {
  'use strict';

  var HomeCtrl = function HomeCtrl($scope, $geolocation) {
    'ngInject';

    _classCallCheck(this, HomeCtrl);

    var vm = this;
    vm.$scope = $scope;
    vm.$geolocation = $geolocation;
    vm.myPosition = 'Something';
    vm.$geolocation.getCurrentPosition({
      timeout: 60000
    }).then(function (position) {
      vm.myPosition = position;
    });
  };

  /**
   * @ngdoc object
   * @name home.controller:HomeCtrl
   *
   * @description
   *
   */


  angular.module('home').controller('HomeCtrl', HomeCtrl);
})();
